body {
  background-image: url("../img/bg.png");
  padding-bottom: 50px;
}

@font-face {
  font-family: 'bebas_neueregular';
  src: url("../fonts/bebas_neue-webfont.woff2") format("woff2"), url("../fonts/bebas_neue-webfont.woff") format("woff");
  font-weight: normal;
  font-style: normal;
}

.page-container {
  display: flex;
  flex-direction: row;
  justify-content: center;
}

.container {
  width: 100%;
  max-width: 960px;
  margin: 0 auto;
  border-right: 10px solid transparent;
  border-left: 10px solid transparent;
}

@media only screen and (max-width: 768px) {
  .container {
    max-width: 430px;
  }
}

.container_flex {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}

@media only screen and (max-width: 768px) {
  .container_flex {
    display: block;
  }
}

.container_center {
  align-items: center;
}

a {
  text-decoration: none;
}

.main {
  margin-top: 25px;
}

.sections {
  width: 70%;
}

@media only screen and (max-width: 768px) {
  .sections {
    width: 100%;
  }
}

.section {
  position: relative;
}

.content {
  margin-top: 25px;
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  flex-wrap: wrap;
}

.image {
  width: 100%;
}

.image img {
  width: inherit;
}

.info {
  width: 22%;
  margin-right: 3%;
}

@media only screen and (max-width: 768px) {
  .info {
    display: flex;
    width: 100%;
    margin-right: 0;
  }
}

@media only screen and (max-width: 768px) {
  .item {
    margin-left: 10px;
  }
}

.item__title {
  font-family: "bebas_neueregular", sans-serif;
  color: #e8603c;
  font-size: 12px;
}

.item__link {
  display: inline-block;
  color: #bababa;
  font-size: 12px;
  margin: 10px 0 15px 0;
  font-family: Helvetica, arial, sans-serif;
}

.topic {
  width: 75%;
}

@media only screen and (max-width: 768px) {
  .topic {
    width: 100%;
  }
}

.topic__desc {
  font-family: Helvetica, arial, sans-serif;
  font-size: 12px;
}

.topic__desc p {
  margin-top: 25px;
  color: #464646;
}

.topic_100 {
  width: 100%;
}

.topic__heading {
  font-size: 18px;
  font-family: "bebas_neueregular", sans-serif;
  color: #e8603c;
}

.site {
  color: #a0a0a0;
  font-family: "Myriad Pro", sans-serif;
  font-size: 12px;
  font-weight: 400;
  line-height: 18px;
}

.more {
  color: #464646;
}

.bottomline {
  height: 13px;
  max-width: 659px;
  margin: 55px 0;
}

@media only screen and (max-width: 768px) {
  .bottomline {
    margin: 20px 0;
  }
}

.quote {
  font-family: Helvetica, arial, sans-serif;
  background-color: whitesmoke;
  display: flex;
  padding: 20px;
  font-style: italic;
}

.quote-icon {
  margin: 20px 20px 0 0;
  color: #e8603c;
  font-family: Georgia;
  font-size: 72px;
  font-weight: 400;
  line-height: 18px;
}

.icon {
  width: 75px;
  height: 75px;
