.main {
  margin-top: 25px; }

.sections {
  width: 70%;
  @include r(768) {
  	width: 100%; } }

.section {
  position: relative; }

.content {
  margin-top: 25px;
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  flex-wrap: wrap; }

.image {
	width: 100%;
	img {
		width: inherit; } }

.info {
	width: 22%;
	margin-right: 3%;
	@include r(768) {
		display: flex;
		width: 100%;
		margin-right: 0; } }

.item {
	@include r(768) {
		margin-left: 10px; }
	&__title {
		font-family: $theme_font;
		color: $theme_color;
		font-size: 12px; }
	&__link {
		display: inline-block;
		color: #bababa;
		font-size: 12px;
		margin: 10px 0 15px 0;
		font-family: $text_font; } }

.topic {
	width: 75%;
	@include r(768) {
		width: 100%; }
	&__desc {
		font-family: $text_font;
		font-size: 12px;
		p {
			margin-top: 25px;
			color: $text_color; } } }

.topic_100 {
  width: 100%; }

.topic__heading {
  font-size: 18px;
  font-family: $theme_font;
  color: $theme_color; }

.site {
  color: #a0a0a0;
  font-family: "Myriad Pro", sans-serif;
  font-size: 12px;
  font-weight: 400;
  line-height: 18px; }

.more {
  color: $text_color; }

.bottomline {
  height: 13px;
  max-width: 659px;
  margin: 55px 0;
  @include r(768) {
  	margin: 20px 0; } }

.quote {
  font-family: $text_font;
  background-color: whitesmoke;
  display: flex;
  padding: 20px;
  font-style: italic; }

.quote-icon {
  margin: 20px 20px 0 0;
  color: $theme_color;
  font-family: Georgia;
  font-size: 72px;
  font-weight: 400;
  line-height: 18px; }

.icon {
	width: 75px;
	height: 75px;
	background-position: center;
	background-repeat: no-repeat;
	background-color: $theme_color;
	position: absolute;
	top: 0;
	left: -85px;
	@include r(1110) {
		display: none; }
	&_photo {
		background-image: url("../img/photo.png"); }
	&_file {
		background-image: url("../img/file.png"); }
	&_quote {
		background-image: url("../img/quote.png"); }
	&_link {
		background-image: url("../img/link.png"); } }

.aside {
	margin-left: 5%;
	width: 25%;
	@include r(768) {
		margin: 40px 0 0 0;
		width: 100%; }
	&__heading {
		display: flex;
		flex-direction: row;
		justify-content: flex-start;
		align-items: stretch;
		font-family: $theme_font;
		color: $theme_color;
		h3 {
			margin-right: 15px; } }
	&__content {
		font-family: $text_font;
		margin: 20px 0 35px 0;
		color: $text_color;
		a {
			color: $text_color; } }
	&__image {
		display: inline-block;
		height: 50px;
		width: 50px;
		border: 2px solid red;
		background-color: $theme_second_color;
		a {
			display: block;
			height: 100%;
			width: 100%; }
		@include r(873) {
			height: 80px;
			width: 80px; }
		@include r(355) {
			height: 110px;
			width: 110px; } } }

.dots {
  background-image: url("../img/dots.png");
  width: inherit;
  height: inherit;
  flex-grow: 1; }

.pagination {
	display: flex;
	&__item {
		margin-right: 5px; }
	&__link {
		font-size: 14px;
		font-family: $theme_font;
		color: white;
		background: $theme_second_color;
		padding: 10px 15px;
		&_active {
		  	background-color: $theme_color; }
		&:hover {
		  	background-color: $theme_color; } } }
